import React from 'react';
import {ICONS} from './icons.js'
import StatusBar from './StatusBar.js'
import Button from '@material-ui/core/Button'
import StudentFinder from './StudentFinder.js'
import StudentInfo from './StudentInfo.js'

import './css/checkIn.css'

export default class CheckIn extends React.Component {
  render() {
    return (
      <div className="check-in card card-default">

        <div className="card-header">

          <div className="card-title">
            <i className={ICONS.userCheck}></i>
            Реєстрація виборця
          </div>

        </div>

        <div className="card-block">

          {this.props.sessionStatus === 100 &&
            <StudentFinder
              onScanStart={this.props.onScanStart.bind(this)}
            />
          }

          {this.props.sessionStatus === 200 &&
            <StudentInfo
              studentInfo={this.props.studentInfo}
            />
          }

          <div className="check-in-controls">
            {this.props.sessionStatus === 200 &&
              <Button onClick={this.props.onCompleteSession} variant="contained" color="primary">завершити</Button>
            }
            <Button onClick={this.props.onCancelSession} >скасувати сесію</Button>
          </div>



        </div>

      </div>
    )
  }

}

